.accordion {
  margin: 0 auto 0 auto;
  display: grid;
  gap: 12%;
  grid-template-columns: auto auto;

  @media (max-width: $vp-1023) {
    gap: 17%;
    grid-template-columns: 230px auto;
  }

  @media (max-width: $vp-767) {
    display: block;
  }

  & & {
    width: 90%;
    margin: 0 auto;
  }

  &__element:last-child {
    @media (max-width: $vp-767) {
      margin-bottom: 0;
    }
  }

  &__element {
    padding: 10px;

    @media (max-width: $vp-1023) {
      padding-left: 0;
    }

    @media (max-width: $vp-767) {
      border-top: 1px solid rgba(248, 253, 255, 0.7);
      position: relative;
      padding-top: 11px;
      padding-bottom: 12px;
      padding-left: 15px;
    }
  }

  &__button {
    display: none;

    @media (max-width: $vp-767) {
      width: 100%;
      font-family: $roboto;
      padding: 10px;
      font-size: 15px;
      line-height: 16px;
      color: $color-default-white;
      display: flex;
      align-items: center;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      background-color: $black-pearl;
      border: none;
      cursor: pointer;
      position: relative;
    }
  }

  &__wrapper {
    color: $color-default-white;
  }
}

.accordion__button::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 17px;
  width: 15px;
  height: 1px;
  background-color: $color-alice-blue;
  transform: rotate(-270deg);
}

.accordion__element--nojs .accordion__button::before {
  content: "";
  position: absolute;
  width: 0;
}

.accordion__element--nojs .accordion__button::after {
  content: "";
  position: absolute;
  width: 0;
}

.accordion__button::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 17px;
  width: 15px;
  height: 1px;
  background-color: $color-alice-blue;
}

.accordion__element.is-active > .accordion__button::after {
  transform: rotate(-180deg);
  content: "";
  position: absolute;
  top: 50%;
  right: 17px;
  width: 15px;
  height: 1px;
  background-color: $color-alice-blue;
}

.accordion__content {
  display: flex;
}

.accordion__wrapper h3 {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: $roboto;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  @media (max-width: $vp-1023) {
    margin-bottom: 22px;
  }

  @media (max-width: $vp-767) {
    display: none;
  }
}

.accordion__link {
  font-family: $mono;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);

  @media (max-width: $vp-767) {
    font-size: 14px;
    line-height: 135%;
  }
}

.accordion__link:hover {
  opacity: 0.8;
}

.accordion__link:focus {
  opacity: 0.8;
}

.accordion__link:active {
  opacity: 0.6;
}

.accordion__address {
  font-family: $mono;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  font-style: normal;

  @media (max-width: $vp-767) {
    font-size: 14px;
    line-height: 135%;
    margin-bottom: 18px;
  }
}

.accordion__list {
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  display: grid;
  column-gap: 9%;
  row-gap: 5px;

  @media (max-width: $vp-1023) {
    display: block;
  }

  @media (max-width: $vp-767) {
    padding-left: 10px;
    padding-top: 8px;
  }
}

.accordion__list .accordion__item {
  list-style: none;

  &:nth-child(-n+4) {
    grid-column: 1 / 2;
  }

  &:nth-child(n+4) {
    grid-column: 1 / 2;
  }

  &:nth-child(n+5) {
    grid-column: 2 / 2;
    grid-row: 1 / 4;
  }

  &:nth-child(n+6) {
    grid-column: 2 / 2;
    grid-row: 2 / 4;
  }

  &:nth-child(n+7) {
    grid-column: 2 / 2;
    grid-row: 3 / 4;
  }

  @media (max-width: $vp-1023) {
    margin-bottom: 5px;
  }
}

.accordion__element--nojs .accordion__content[data-accordion="content"] {
  max-height: unset;
}
